<template>
  <transition>
    <song-page :songs="songsList" :bg-image="bgImage" :title="title"></song-page>
  </transition>
</template>

<script>
import { mapGetters } from 'vuex'
import { createSong } from '@/common/js/song'
import getSingerDetail from '@/api/singerdetail'
import SongPage from './song'
export default {
  components: {
    SongPage
  },
  data() {
    return {
      songsList: []
    }
  },
  computed: {
    ...mapGetters([  //singer存储歌手详细信息
      'singer'
    ]),
    bgImage() {
      return this.singer.pic
    },
    title() {
      return this.singer.name
    }
  },
  created() {
    this._getSingerDetail()
  },
  methods: {
    // 获取歌手详细信息(歌曲信息musicData)
    async _getSingerDetail() {

      if (!this.singer.id) {  //点击歌手时才可获取到歌手id值，那么在歌手详情页刷新页面无法获得id，需要返回歌手列表页面
        this.$router.push({
          path: '/singer'
        })
        return
      }
      try {
        let res = await getSingerDetail(this.singer.id);
        this.songsList = this._sortSongsList(res.data.list)
        console.log(this.songsList);
      } catch (error) {
        console.log(err)
      }
    },

    _sortSongsList(list) {
      let ret = []
      list.forEach((item, index) => {
        let musicData = item.musicData //只需要musicData的数据
        if (musicData.songid && musicData.albummid) {
          ret.push(createSong(musicData))
        }
      })
      return ret
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~styles/variable"
  .v-enter-active, .v-leave-active
    transition: all 0.5s
  .v-enter, .v-leave-to
    transform: translate3d(100%, 0, 0)
</style>



